.navbar-brand {
    background: teal;
    color: white;
  }
  
  .jumbotron {
    background-color: transparent;
  }
  
  body {
    background-image: url("https://user-images.githubusercontent.com/73902490/101559559-5be87680-398f-11eb-828c-ee9fe6458478.png");
  }
  
  .p1 {
    display: flex;
    flex-direction: column;
  }
  
  .p2 {
    flex-direction: row;
    display: flex;
  }
  
  .p3 {
    flex-direction: row;
    display: flex;
  }
  
  h1 {
      color: teal;
  }
  
  input[type=text], select, textarea {
      width: 100%; 
      padding: 10px;  
      border: 1px solid #ccc; 
      border-radius: 00px; 
      box-sizing: border-box; 
      margin-top: 0px; 
      margin-bottom: 0px; 
      resize: vertical; 
    }
    
    input[type=submit] {
      background-color: teal;
      color: white;
      padding: 6px 10px;
      border: none;
      border-radius: 2px;
      cursor: pointer;
    }
    
    input[type=submit]:hover {
      background-color: #45a049;
    }
    
    .container {
      border-radius: 0px;
      background-color: #f2f2f2;
      padding: 20px;
    }
  
    .footer {
      background: teal;
    }